.navbar {
    height: 55px;
    box-shadow: 0 3px 6px var(--shadow-color);
    background-color: var(--background-color);
    color: var(--text-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
z-index: 998;
position: fixed;
width: 100%;
top: 0;

    .logo {
        font-size: 1.7em;
        font-weight: bolder;
        color: var(--primary-color);
        text-shadow: 0 1px 3px var(--shadow-color);

        span {
            color: var(--text-color);
        }
    }


    .nav-links {
        a {
            margin-left: 1em;
            color: var(--text-light);
            letter-spacing: 2px;
            text-transform: uppercase;
            position: relative;
            font-size: .9em;

            &::before {
                content: '';
                position: absolute;

                display: inline-block;
                height: 3px;
                width: 100%;
                background-color: var(--primary-color);

                bottom: -2px;
                left: 0;
                transform: scaleX(0);
                transition: transform getanimationduration(.2s) ease-in-out;
            }

            &:hover {
                &::before {
                    transform: scaleX(1);
                }
            }

            &.active {
                color: var(--primary-color);
                font-weight: bold;
            }
        }
    }

    .sidebar-btn {
        height: 30px;
        width: 35px;
        position: relative;
        display: none;

        .bar {
            height: 6px;
            width: 100%;
            background-color: var(--text-color);
            position: absolute;
            border-radius: 5px;
            transition: transform getanimationduration(.5s);

            &:nth-child(1) {
                top: 0;
            }

            &:nth-child(2) {
                top: 50%;
                transform-origin: right;

                transform: scaleX(0.8) translateY(-50%);
            }

            &:nth-child(3) {
                transform-origin: right;
                top: 100%;
                transform: scaleX(1.1) translateY(-100%);
            }
        }

        &.active {
            .bar {
                transform-origin: center;
                top: 50%;

                &:nth-child(1) {
                    transform: translate(0, -50%) scale(1) rotate(45deg);
                }

                &:nth-child(3) {
                    transform: translate(0, -50%) scale(1) rotate(-45deg);
                }

                &:nth-child(2) {
                    transform: scaleX(0) translateY(-50%);
                }

            }
        }
    }
}

.sidebar {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 250px;
    background-color: var(--background-color);
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.2), 3px 0 5px 1000px var(--shadow-color);
    animation: getanimationduration(.3s) slidein;
    .sidebarlink{
        color: var(--text-color);
        display: block;
        padding: .5em 1em;
        border-left: .4em solid transparent;
        margin-bottom: 1em;
        font-size: 1.1em;
        transform: translateX(-100%);
    animation: getanimationduration(.5s) slidein forwards;
@for $i from 1 through 3{
    &:nth-child(#{$i}){
        animation-delay: #{$i * .1}s ;
    }
}
        &.active{
            border-left-color: var(--primary-color);
            
        }
        svg{
            width: 1.2em;
            margin-right: .5em;
        }
        &:hover{
border-left-color: var(--primary-color);
background-color: rgba(0,0,0,0.05);
        }
    }
}